.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .5;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 50em;
        max-width: calc(100% - 1em);
        position: relative;
        z-index: 3;
        padding: 2.5em 3.5em;
        display: inline-block;
    }
    .close-btn{
        position: absolute;
        top: 1em;
        right: 1em;
    }
}

.close-btn{
    width: 1.5em;
    @include center;
    img{
        width: 100%;
    }
}

.modal-form{
    &__grid{
        display: grid;
        grid-template-columns: repeat(2,calc(50% - 1.25em));
        gap: 2.5em;
    }
    &__fields{
        display: grid;
        grid-template-columns: calc(100% - 15em) 12.5em;
        gap: 2.5em;
    }
}

.ui-timepicker-standard{
    z-index: 101 !important;
}



@media screen and (max-width: 992px){
    .modal{
        &__dialog {
            padding: 2em 1em;
        }
        .close-btn{
            top: .5em;
            right: .5em;
        }
    }

    .modal-form{
        &__grid{
            grid-template-columns: repeat(2,calc(50% - .75em));
            gap: 1.5em;
        }
        &__fields{
            grid-template-columns: calc(100% - 14em) 12.5em;
            gap: 1.5em;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {

        }
    }

    .modal-form{
        &__grid,
        &__fields{
            grid-template-columns: 100%;
        }
    }

}
