.text-center{
  text-align: center;
}

.caps-text{
  text-transform: uppercase;
}

.underline-text{
  text-decoration: underline;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6{
  line-height: 120%;
  text-transform: uppercase;
}

.h1{
  font-size: 3.25em;
  font-weight: 800;
}

.h2{
  font-size: 2.25em;
  font-weight: 800;
}

.h3{
  font-size: 2em;
  font-weight: 600;
}

.h4{
  font-size: 1.75em;
  font-weight: 600;
}

.h5{
  font-size: 1.375em;
  font-weight: 500;
}

.h6{
  font-size: 1.25em;
  font-weight: 500;
}

.quote-text{
  font-size: 1.375em;
  line-height: 150%;
  font-style: italic;
}

.text p,
.m-list li,
.footer-nav a,
.text-l,
.text18{
  font-size: 1.125em;
  line-height: 150%;
}

.text--xl p,
.text-xl,
.text20{
  font-size: 1.25em;
  line-height: 150%;
}

.text19{
  font-size: 1.1875em;
  line-height: 120%;
}



.text17{
  font-size: 1.0625em;
  line-height: 120%;
}

.text--m p,
.nav a,
.text-m,
.text16{
  font-size: 1em;
  line-height: 150%;
}

.text15{
  font-size: .9375em;
  line-height: 120%;
}

.text-s,
.text14{
  font-size: .875em;
  line-height: 150%;
}

.text-xs,
.text13{
  font-size: .8125em;
  line-height: 150%;
}

.text-xxs,
.text12{
  font-size: .75em;
  line-height: 150%;
}

.text11{
  font-size: .6875em;
  line-height: 120%;
}

.text-xxxs,
.text10{
  font-size: .625em;
  line-height: 150%;
}

.extra-bold-text{
  font-weight: 800;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.regular-text{
  font-weight: 400;
}

.light-text{
  font-weight: 300;
}

.neutral-text{
  color: $neutralText;
}

.black-medium-text{
  color: $blackMedium;
}

.green-text{
  color: $primary;
}

.green-text2{
  color: $secondary;
}

.font2{
  font-family: $font2;
}

._no-wrap{
  white-space: nowrap;
}

.not-upper-text{
  text-transform: unset;
}

sup{
  font-size: .5em;
}


@media screen and (max-width: 992px){
  .h1{
    font-size: 1.875em;
  }

  .h2{
    font-size: 1.75em;
  }

  .h3{
    font-size: 1.5em;
  }

  .h4{
    font-size: 1.375em;
  }

  .h5{
    font-size: 1.125em;
  }

  .h6{
    font-size: 1em;
  }

  .text20-tablet{
    font-size: 1.25em;
  }

  .text19-tablet{
    font-size: 1.1875em;
  }

  .quote-text,
  .text--xl p,
  .text-xl,
  .text18-tablet{
    font-size: 1.125em;
  }

  .text17-tablet{
    font-size: 1.0625em;
  }

  .text p,
  .footer-nav a,
  .text-l,
  .text16-tablet{
    font-size: 1em;
  }

  .text15-tablet{
    font-size: .9375em;
  }

  .text--m p,
  .m-list li,
  .text-m,
  .text14-tablet{
    font-size: .875em;
  }

  .text-s,
  .text13-tablet{
    font-size: .8125em;
  }

  .text12-tablet{
    font-size: .75em;
  }

  .text11-tablet{
    font-size: .6875em;
  }

  .text-xxs,
  .text-xs,
  .text10-tablet{
    font-size: .625em;
  }

}
