.filter{
  padding: .5em;
  &__inner{
    padding: .25em;
    border: 1px solid #E5E5E7;
    border-radius: 1em;
    background-color: $white;
    display: flex;
    align-items: stretch;
    gap: .25em;
  }
  .fg{
    select,
    input{
      padding-top: 0;
      font-size: .875em;
      min-height: 100%;
      &::placeholder{
        color: $blackMedium;
      }
    }
  }
  &__condition{
    width: 24.625em;
  }
  &__district,
  &__street{
    width: 15em;
  }
  &__range{
    width: 16.625em;
    label{
      top: 50%;
      transform: translateY(-50%);
    }
    input{
      padding-left: 5.25em;
    }
    .fg-slider{
      padding: 0 .75em;
    }
  }
}

.rooms{
  padding: .25em;
  border: 1px solid #E5E5E7;
  height: auto;
  border-radius: .875em;
  display: flex;
  align-items: center;
  background-color: #F8F9FB;
  gap: .25em;
  &__title{
    padding: 0 1em;
  }
  &__item{
    width: 3em;
    flex-shrink: 0;
    label{
      @include center;
      height: 3em;
      width: 100%;
      border-radius: 1em;
      border: 1px solid transparent;
      transition: all .3s;
    }
    input:checked~label{
      background-color: $white;
      border-color: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .filter{
    padding: .25em;
    &__inner{
      display: grid;
      grid-template-columns: 100%;
    }
    .fg{
      font-size: 1em;
    }
    &__range,
    &__condition,
    &__district,
    &__street{
      width: 100%;
    }
    &__btn{
      width: 100%;
    }
    &__range{
      .text-s{
        font-size: .875em;
      }
    }
  }

  .rooms{
    &__title{
      padding: 0 .5em;
    }
    &__item{
      width: 2.5em;
      flex-grow: 1;
      label{
        height: 2.5em;
      }
      span{
        font-size: .8125em;
      }
    }
  }
}


