@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.5em;
    height: 3.5em;
    border-radius: 1em;
    font-weight: 500;
    img{
        width: 1.25em;
        margin: 0 .5em;
        &:first-child{
            margin-left: 0;
        }
        &:last-child{
            margin-right: 0;
        }
    }
    &:focus {
        box-shadow: none;
    }
}

.m-btn-green{
    background-color: $secondary;
    color: $white;
    &:hover{
        background-color: darken($secondary,10%);
        color: $white;
    }
}

.m-btn-gray-outline{
    border: 1px solid #E5E5E7;
    background-color: $white;
    color: $black;
    &:hover{
        background-color: darken($white,10%);
        color: $black;
    }
}



@media screen and (max-width: 992px){
    .m-btn{
        height: 3em;
        span{
            font-size: .875em;
        }
    }
}


