@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
@import "components/filter-slider.scss";
._flex,
._flex-between,
._flex-center{
  display: flex;
  align-items: center;
  &._start{
    align-items: unset;
  }
}

._flex-between{
  justify-content: space-between;
}

._flex-center{
  justify-content: center;
}

._mt64{
  margin-top: 4em;
}

._mt48{
  margin-top: 3em;
}

._mt40{
  margin-top: 2.5em;
}

._mt32{
  margin-top: 2em;
}

._mt24{
  margin-top: 1.5em;
}

._mt16{
  margin-top: 1em;
}

._mt12{
  margin-top: .75em;
}

._mt8{
  margin-top: .5em;
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}

.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 0%, rgba(0, 0, 0, 0.40) 100%);
    pointer-events: none;
    border-radius: inherit;
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 2.5em;
    height: 2.5em;
    @include lastM(0 1em 0 0);
    @include center;
    img{
      width: 100%;
      transition: all .3s;
    }
    &:hover{
      img{
        transform: translateY(-15%);
      }
    }
  }
}




@media screen and (max-width: 992px){
  ._mt40-tablet{
    margin-top: 2.5em;
  }

  ._mt24-tablet{
    margin-top: 1.5em;
  }

  ._mt16-tablet{
    margin-top: 1em;
  }

  ._mt8-tablet{
    margin-top: .5em;
  }

  ._mt4-tablet{
    margin-top: .25em;
  }

}


