.house-main{
  position: relative;
  &:after{
    background: linear-gradient(270deg, rgba(0, 0, 0, 0.00) 31.04%, rgba(0, 0, 0, 0.56) 70.05%);
  }
  &__video{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.house-main-slider{
  overflow: hidden;
  width: 47.25em;
  z-index: 3;
  .swiper-pagination{
    position: absolute;
    bottom: 4em;
    left: -.25em;
    width: auto;
    &-bullet{
      width: 5.875em;
      height: .375em;
      &-active{
        background-color: $secondary;
      }
    }
  }
}

.main-card2{
  color: $white;
  min-height: 43.75em;
  padding: 4em 0 6em;
  &__tags{
    display: flex;
    flex-wrap: wrap;
    margin-top: -1em;
  }
  &__tag{
    margin: 1em 1em 0 0;
    padding: 0 .75em;
    height: 1.75em;
    display: flex;
    align-items: center;
    border-radius: 1em;
    border: 1px solid #E5E5E7;
    background: rgba(0, 0, 0, 0.30);
  }
}


@media screen and (max-width: 992px){
  .house-main-slider{
    width: 100%;
    .swiper-pagination{
      bottom: 2.5em;
      width: 100%;
      &-bullet{
        width: 4.875em;
        flex-grow: 1;
      }
    }
  }

  .main-card2{
    padding: 2.5em 0 4em;
    width: 18.0625em;
    &__tags{
      margin-top: -.5em;
    }
    &__tag{
      margin: .5em .5em 0 0;
      padding: 0 .5em;
      height: 1.4375em;
    }
  }
}


