.consult{
  padding: 4em 0;
  background-color: $bg;
  overflow: hidden;
  &__inner{
    position: relative;
  }
  &__content{
    width: 47.25em;
    max-width: 100%;
  }
  &__img{
    position: absolute;
    width: 73.75em;
    top: 0;
    left: 48.75em;
    img,
    svg{
      display: block;
      width: 100%;
    }
  }
}

.consult-form{
  &__grid{
    display: grid;
    grid-template-columns: calc(50% - 7.5em) calc(50% - 7.5em) 13em;
    gap: 1em;
  }
  &__btn{
    padding: 0;
  }
}

.m-section{
  padding: 8em 0;
  &._first{
    padding-top: 1.5em;
  }
}

.reviews{
  &__top{
    max-width: 58.5em;
    margin-bottom: 4em;
  }
}

.info-text{
  max-width: 58.5em;
  &--medium{
    max-width: 47.25em;
  }
  &--small{
    max-width: 41em;
  }
}

.banner{
  padding: 1.5em 0;
  background: $bg;
  &__col{
    padding: 3.25em 0 3.25em 2em;
  }
  &__img{
    height: 17.375em;
    @include imgCover(0);
    border-radius: 1em;
  }
  .m-btn{
    padding: 0 2.5em;
  }
}

.live-complex{
  background-color: $bgLight;
  &--white{
    background-color: transparent;
  }
  &__top{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
}

.house-card{
  position: relative;
  font-weight: 500;
  color: $white;
  display: block;
  border-radius: 1em;
  box-shadow: $shadowXl;
  &:hover{
    color: $white;
    .house-card__img{
      img{
        transform: scale(1.05);
      }
    }
  }
  &__img{
    @include imgCover(100%);
    border-radius: 1em;
    overflow: hidden;
    img{
      transition: all .6s;
    }
  }
  &__badge{
    position: absolute;
    top: .5em;
    left: .5em;
    padding: 0 .75em;
    height: 1.75em;
    display: flex;
    align-items: center;
    border-radius: 1em;
    background: $primary;
    font-weight: 500;
    color: $white;
    z-index: 2;
    pointer-events: none;
  }
  &__info{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    padding: 1.5em;
    z-index: 2;
  }
  &__slider{
    &~.house-card__info{
      padding-bottom: 3.625em;
    }
  }
  .slider__wrap{
    border-radius: 1em;
  }
  .swiper-pagination{
    bottom: 1.5em;
    left: 0;
    justify-content: unset;
    padding: 0 1.25em;
  }
}


@media screen and (max-width: 992px){
  .consult{
    padding: 2.5em 0;
    &__content{
      width: 100%;
    }
    &__img{
      position: static;
      width: 100%;
      margin-top: 1em;
    }
  }

  .m-section{
    padding: 4em 0;
    &._first{
      padding-top: 4em;
    }
  }

  .reviews{
    &__top{
      margin-bottom: 2.5em;
    }
  }

  .banner {
    &__col {
      padding: 0;
    }
  }

  .house-card{
    &__info{
      padding: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .consult-form{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .live-complex{
    &__top{
      flex-direction: column;
      align-items: flex-start;
      .m-btn{
        margin-top: 2.5em;
      }
    }
  }
}
