.filter-slider{
  @include lastM(0 0 .75em);
  &__item{
    min-height: .125em;
    display: flex;
    align-items: center;
  }
  .range-slider{
    width: 100%;
    background-color: #E5E5E7;
    border: none;
    box-shadow: none;
    height: .125em;
    border-radius: 0;
    .noUi-connect{
      background-color: $primary;
    }
    .noUi-handle{
      box-shadow: none;
      border: none;
      background-color: $primary;
      width: .875em;
      height: .875em;
      top: -.375em !important;
      right: -.375em !important;
      border-radius: 50%;
      cursor: pointer;
      &:before{
        opacity: 0;
        width: 300%;
        height: 100%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
}
