.footer{
  padding: 4em 0 1.25em;
  color: $white;
  background-color: $primary;
  &__top{
    display: flex;
    padding-top: 1.5em;
    gap: 1.5em;
    &-col{
      margin-right: 1em;
      margin-top: -1.5em;
    }
    &-col4{
      max-width: 26em;
    }
  }
  &__logo{
    display: block;
    width: 15.3125em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__bottom{
    padding-top: 1em;
    border-top: 1px solid #E5E5E7;
  }
}

.footer-item{
  display: flex;
  &__icon{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-shrink: 0;
    margin-right: .5em;
    width: 2.5em;
    padding-top: 1.31em;
    img{
      width: 1.5em;
    }
  }
}

.footer-nav{
  ul{
    display: flex;
    gap: 1.5em;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 2.5em 0 .625em;
    &__top{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .75em));
      padding: 0;
      &-col{
        margin: 0 0 2em;
        grid-column: 1/-1;
      }
    }
    &__medium{
      flex-direction: column;
    }
    &__bottom{
      padding-top: .5em;
    }
  }

  .footer-nav{
    margin-bottom: 1.5em;
    ul{
      gap: 1em;
      flex-wrap: wrap;
      justify-content: center;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__top{
      grid-template-columns: 100%;
    }
    &__bottom{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
  }
}
