.main-banners{
  padding: 1.5em 0;
  overflow: hidden;
}

.news-banner{
  @include imgCover(136.6%);
  border-radius: 1em;
}

.main{
  padding: 1.5em 0;
  background: rgba(239, 235, 226, 0.20);
  overflow: hidden;
}

.main-slider{
  .swiper-slide{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
  }
  .swiper-slide-active{
    opacity: 1;
    pointer-events: unset;
    .main-card__title,
    .main-card__desc,
    .main-card__nav{
      transform: translateY(0);
      opacity: 1;
    }
    .main-card__img{
      opacity: 1;
      transform: scale(1);
    }
  }
  .swiper-pagination{
    justify-content: unset;
    margin-left: -.25em;
    &-bullet{
      width: 5.875em;
      height: .375em;
    }
  }
  @media screen and (min-width: 993px){
    .swiper-pagination{
      position: absolute;
      left: 0;
      bottom: 2.5em;
      width: 50%;
    }
  }
}

.main-card{
  &__col{
    padding: 4em 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  &__img{
    opacity: 0;
    transition: all .3s;
    transition-delay: .3s;
    @include imgCover(73%);
    border-radius: 6em 0;
    min-height: 100%;
    transform: scale(.7);
  }
  &__title,
  &__desc,
  &__nav{
    transition: all .4s;
    transform: translateY(3em);
    opacity: 0;
  }
  &__title{
    transition-delay: .4s;
  }
  &__desc{
    transition-delay: .7s;
  }
  &__nav{
    gap: 1em;
    transition-delay: 1.1s;
  }
  .m-btn{
    span{
      font-size: 1.125em;
    }
  }
}

.req{
  &__col2{
    grid-column: span 2;
  }
}

.calc{
  @include grid100gap(2.5em);
  .fg{
    input,
    select{
      background-color: transparent;
    }
  }
}

.fg-slider{
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 0 1.5em;
  width: 100%;
}

.switch-group{
  display: flex;
  &__col{
    margin-right: 1em;
  }
}


@media screen and (max-width: 992px){
  .main-banners{
    padding: 1em 0;
    .swiper-pagination{
      display: none;
    }
  }

  .main{
    padding: 1em 0;
  }

  .main-slider{
    .swiper-pagination{
      margin-top: 2.5em;
      width: calc(100% + .5em);
      &-bullet{
        flex-grow: 1;
      }
    }
  }

  .main-card{
    &__col{
      padding: 0;
    }
    &__nav{
      display: grid;
      grid-template-columns: 100%;
    }
    .m-btn{
      width: 100%;
      height: 2.5em;
      span{
        font-size: .8125em;
      }
    }
  }


  .req{
    &__col2{
      grid-column: span 1;
    }
  }

  .calc{
    gap: 1.5em;
  }

  .fg-slider{
    padding: 0 1em;
  }
}


@media screen and (max-width: 576px){
  .main-card{
    grid-template-columns: 100%;
    &__col2{
      order: -1;
    }
    &__img{
      height: 18.75em;
      padding: 0;
    }
  }
}
