.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    position: absolute;
    pointer-events: none;
    top: .375em;
    left: calc(1em + 1px);
    color: $blackMedium;
  }
  select,
  .nice-select,
  input,
  textarea{
    border-radius: 1em;
    border: 1px solid #E5E5E7;
    padding: 1em 1em 0;
    height: 3.5em;
    width: 100%;
    font-size: 1em;
    line-height: 150%;
    background-color: #F8F9FB;
    color: $blackMedium;
    &::placeholder {
      color: #929798;
    }
    &:disabled{
      pointer-events: none;
      background: #F6F6F6 !important;
      color: rgba(#000,.4);
    }
  }
  textarea{
    padding-top: 1.69em;
    height: 11.1875em;
    resize: none;
  }
  select{
    font-family: $font1;
    @include removeSelectArrow;
    background-image: url(../img/select-arrow.svg);
    background-size: 1em auto;
    background-position: right 1em center;
    background-repeat: no-repeat;
  }
  &__icon{
    position: absolute;
    top: 1em;
    left: 1em;
    width: 1.5em;
    &~input{
      padding-left: 3em;
    }
    &~label{
      left: calc(3em + 1px);
    }
  }
  &__field{
    position: relative;
  }
  &._error{
    input,
    textarea{
      border-color: $primary;
      color: $primary;
      &::placeholder{
        color: $primary;
      }
    }
    .fg__error-text{
      display: block;
    }
  }
  &__error-text{
    margin-top: .375em;
    color: $primary;
    display: none;
  }
}

.rooms__item,
.m-switch,
.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  font-family: $font2;
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 1.5em;
    min-height: 1.5em;
    padding-top: .1875em;
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: .25em;
      border: 1px solid $secondary;
      width: 1em;
      height: 1em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-size: .625em auto;
      }
    }
  }
  label {
    &:before {
      border-radius: .125em;
      background-image: url(../img/chec2.svg);
    }
  }
  &--green{
    input {
      &:checked+label {
        &::before {
          background-color: $primary;
          border-color: $primary;
        }
      }
    }
    label {
      &:before {
        border-radius: .375em;
        background-image: url(../img/check3.svg);
      }
    }
  }
}

.m-switch{
  label{
    border-radius: 1em;
    background: #C8C8C8;
    width: 2.75em;
    height: 1.5em;
    display: flex;
    align-items: center;
    padding: .125em;
    transition: all .3s;
    &:before{
      content: '';
      width: 1.25em;
      height: 1.25em;
      box-shadow: 0 1.029px 2.057px -1.029px rgba(16, 24, 40, 0.10), 0 1.029px 3.086px 0 rgba(16, 24, 40, 0.10);
      background-color: $white;
      border-radius: 50%;
      transition: all .3s;
    }
  }
  input:checked~label{
    background-color: $primary;
    &:before{
      transform: translateX(1.3125em);
    }
  }
}


@media screen and (max-width: 992px){
  .fg{
    font-size: .875em;
  }

  .m-check,
  .m-radio{
    label{
      padding-left: 1.25em;
      padding-top: .28em;
    }
  }
}


