@import "../vars.scss";
.breadcrumbs {
    padding: 1em 0;
    font-weight: 500;
    li{
        &:first-child{
            background-image: url(../img/home.svg);
            height: 1.3125em;
            @include bgImgSize(100%);
            overflow: hidden;
            a{
                opacity: 0;
                display: inline-block;
                width: 1.14em;
                overflow: hidden;
            }
        }
    }
    li {
        position: relative;
        display: inline;
        &:not(:first-child) {
            margin-left: 1.5em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                width: 1.25em;
                height: 1.25em;
                background-image: url(../img/bread-arrow.svg);
                @include bgImgSize(100%);
                top: 0;
                right: 100%;
                margin-right: .25em;
            }
        }
        &:last-child{
            color: $primary;
        }
    }
    a,span{
        font-size: .875em;
        line-height: 150%;
    }
}
