.m-list{
  @include grid100gap(.625em);
  li{
    padding-left: 2.11em;
    position: relative;
    background-image: url(../img/check.svg);
    background-size: 1.22em auto;
    background-repeat: no-repeat;
    background-position: left 0 top .11em;
  }
}

.text{
  p{
    margin-bottom: 1em;
  }
  ol {
    counter-reset: list;
    li {
      counter-increment: list;
      &:before {
        content: counter(list) '. ';
      }
    }
  }
  ul{
    li{
      position: relative;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
}



