.tabs{
  display: flex;
  &__items{
    display: flex;
    padding: .25em;
    border-radius: 1em;
    border: 1px solid #E5E5E7;
  }
}

.tab{
  height: 3em;
  padding: 0 1.5em;
  @include center;
  position: relative;
  font-weight: 500;
  transition: all .3s;
  border-radius: 1em;
  span{
    font-size: 1.125em;
  }
  &._active,
  &:hover{
    background: $secondary;
    color: $white;
    cursor: pointer;
  }
  &._active{
    box-shadow: 0 2px 4px -2px rgba(16, 24, 40, 0.10), 0 4px 6px -1px rgba(16, 24, 40, 0.10);
    pointer-events: none;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}



@media screen and (max-width: 992px){
  .tab{
    height: 2.5em;
    span{
      font-size: .8125em;
    }
  }
}


@media screen and (max-width: 576px){
  .tabs{
    display: block;
    &__items{
      display: block;
    }
  }

  .tab{
    width: 100%;
  }


}

