:root {
    --vh: 1vh;
}

$font1:'TT Norms Pro', sans-serif;
$font2:'Inter', sans-serif;
$font3:'Montserrat', sans-serif;
$black: #181818;
$blackMedium: #2B2D2F;
$white: #fff;
$primary: #057861;
$secondary: #77bb7e;
$bg: #efebe2;
$bgLight: #fcfbf9;
$neutralText: #E5E5E7;
$vh: calc(100 * var(--vh));

$shadowXl: 0 8px 10px -6px rgba(16, 24, 40, 0.10), 0 20px 25px -5px rgba(16, 24, 40, 0.10);

$shadowL: 0 4px 6px -4px rgba(16, 24, 40, 0.10), 0 10px 15px -3px rgba(16, 24, 40, 0.10);

