.header{
  padding: 1.25em 0;
  position: relative;
  .container{
    padding: 0 2em;
  }
  &__inner{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 1em 2em;
    border-radius: 1em;
    background: #F8F9FB;
  }
  &__logo{
    width: 9.5625em;
    display: block;
    flex-shrink: 0;
    margin-right: auto;
    img{
      width: 100%;
      display: block;
    }
  }
}

.menu{
  @include centerAbsolute;
}

.nav{
  font-family: $font2;
  ul{
    display: flex;
    gap: 2em;
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    z-index: 20;
    .container{
      padding: 0 1em;
    }
    &__inner{
      padding: .5em;
      position: relative;
      z-index: 3;
    }
    &__logo{
      width: 7.625em;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .burger{
    @include center;
    width: 2.5em;
    height: 2.5em;
    border-radius: 0.5em;
    background-color: $secondary;
    flex-shrink: 0;
    margin-left: 1em;
    position: relative;
    img{
      @include centerAbsolute;
      transition: all .3s;
      width: 1.25em;
    }
    ._view2 {
      opacity: 0;
    }
  }

  .menu{
    width: 100%;
    border-radius: 0 0 1.5em 1.5em;
    background: $white;
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
    top: 4.75em;
    left: 0;
    transform: unset;
    padding: 1em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }

  .nav{
    ul{
      display: block;
    }
    a{
      font-size: .875em;
      padding: .57em 1.14em;
      display: flex;
      align-items: center;
      &:hover{
        background-color: #F8F9FB;
        color: $black;
      }
    }
  }
}


