.contacts{
  .footer-item{
    color: $blackMedium;
  }
}

.contacts-form{
  &__btn{
    padding: 0 2.5em;
  }
}

._full{
  grid-column: 1/-1;
}

.map{
  height: 52em;
  margin-top: 4em;
  border-radius: 1.5em;
  overflow: hidden;
  .ymaps-2-1-79-balloon{
    font-size: 1em;
    font-family: $font1;
    //box-shadow: none;
    padding: 0;
    //max-width: 15em;
    &__tail{
      box-shadow: none;
      background: unset;
      &:after{
        display: none;
      }
    }
    &__layout{
      box-shadow: none;
      border-radius: 1em;
      padding: 1em;
      background-color: $white;
      position: relative;
    }
    &__content{
      box-sizing: border-box;
      font-family: $font1;
      font-size: 1em;
      padding: 0;
      min-width: 18em;
      margin-right: 0;
      background-color: transparent;
      color: $black;
      &>*{
        width: 100% !important;
        height: unset !important;
      }
    }
    &__close{
      display: none;
    }
  }
}



@media screen and (max-width: 992px){
  .contacts-form{
    &__btn{
      padding: 0 1.5em;
    }
  }

  .map{
    margin-top: 2.5em;
    height: 30em;
  }
}


@media screen and (max-width: 576px){
  .map{
    height: 21.5em;
    .ymaps-2-1-79-balloon{
      &__layout{
        padding: .5em;
      }
      &__content{
        min-width: 15em;
      }
    }
  }
}
