.mortgage-info{
  padding: 2.5em 0;
}

.mortgage-support,
.mortgage-free{
  padding: 2.5em;
  background-color: $bg;
  border-radius: 1.5em;
  &__img{
    margin-top: 1.5em;
    img,
    svg{
      width: 100%;
    }
  }
}

.mortgage-support{
  display: grid;
  grid-template-columns: calc(100% - 21.81em) 20em;
  gap: 1.25em 1.81em;
  background-color: #F8F9FB;
  &__img{
    margin: 0;
  }
}

.house-about{
  padding-top: 2.5em;
  overflow: hidden;
  &__desc{
    max-width: 65em;
  }
  &__more{
    display: flex;
    align-items: center;
    color: $blackMedium;
    margin-top: 1.5em;
    font-weight: 500;
    img{
      width: 1em;
      margin-left: .375em;
      transition: all .3s;
    }
    ._view2{
      display: none;
    }
  }
  &._show-text{
    .house-about__more{
      ._view2{
        display: block;
      }
      ._view1{
        display: none;
      }
      img{
        transform: rotate(-90deg);
      }
    }
  }
  &:not(._show-text){
    .text{
      *:nth-child(1)~*{
        display: none;
      }
    }
  }
}

.about-slider{
  .swiper-slide{
    width: calc(100% / 3);
    opacity: 1;
    pointer-events: unset;
  }
  .swiper-pagination{
    display: none;
  }
}

.about-card{
  border-radius: 1em;
  border: 1px solid #E5E5E7;
  background: $white;
  height: 100%;
  transition: all .3s;
  &:hover{
    box-shadow: $shadowL;
  }
  &__top{
    position: relative;
  }
  &__title{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    padding: 1.5em;
    color: $white;
  }
  &__img{
    border-radius: 1em;
    @include imgCover(72.6%);
    &:after{
      background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 0%, rgba(0, 0, 0, 0.20) 100%);
    }
  }
  &__bottom{
    padding: 1.5em;
    color: $blackMedium;
  }
}


@media screen and (max-width: 992px){
  .mortgage-info{
    padding: 1.5em 0;
  }

  .mortgage-free{
    padding: 1.5em;
    overflow: hidden;
    &__img{
      margin-top: 1.875em;
      height: 14.25em;
      position: relative;
      img,
      svg{
        width: auto;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }

  .mortgage-support{
    padding: 1.5em;
  }

  .house-about{
    padding-top: 1.5em;
    &__more{
      margin-top: 1em;
    }
  }

  .about-slider{
    .swiper-slide{
      width: 19em;
    }
    .swiper-pagination{
      display: flex;
    }
  }

  .about-card{
    &__title{
      padding: 1em;
    }
    &__img{
      height: 13em;
      padding: 0;
    }
    &__bottom{
      padding: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .mortgage-support{
    grid-template-columns: 100%;
  }
}
