.swiper-pagination2{
  position: absolute;
  bottom: 1em;
  left: 0;
  width: 100%;
  .swiper-pagination-bullet{
    width: .625em;
    height: .625em;
    &-active{
      background-color: $secondary;
      border-color: $secondary;
    }
  }
}

.mortgage{
  background-color: $bgLight;
  .text{
    column-count: 2;
    column-gap: 1.5em;
    margin-bottom: -1em;
  }
}

.mortgage-slider{
  @media screen and (min-width: 993px){
    .swiper-container{
      width: 100%;
    }
    .swiper-slide{
      padding: 0;
      &._big{
        grid-column: span 2;
      }
    }
    .swiper-wrapper{
      transform: unset !important;
      display: grid;
      gap: 1.5em;
      grid-template-columns: repeat(4,calc(25% - 4.5em / 4));
    }
  }
}

.mortgage-card{
  padding: 1.5em;
  border-radius: 1em;
  background-color: $primary;
  color: $white;
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: 31.25em;
  position: relative;
  &--img{
    background-color: transparent;
    color: $black;
    .mortgage-card__top{
      color: $primary;
    }
  }
  &__bg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 1em;
    overflow: hidden;
    &:after{
      background: linear-gradient(279deg, rgba(252, 251, 249, 0.00) 22.05%, #FCFBF9 40.65%);
    }
    img{
      margin-left: auto;
      height: 100%;
      width: auto;
      display: block;
    }
  }
  &__top,
  &__bottom{
    position: relative;
    z-index: 2;
    max-width: 29em;
  }
  &__bottom{
    margin-top: auto;
    padding-top: 2em;
  }
}

.qa-card{
  border-radius: 1em;
  border: 1px solid #E5E5E7;
  &._active{
    .qa-card__btn{
      background-size: 0 auto, 100% auto;
    }
    .qa-card__title{
      transition: all .3s;
    }
  }
  &__show{
    cursor: pointer;
    display: flex;
    align-items: center;
    padding: 1.5em;
  }
  &__title{
    transition: all .3s;
  }
  &__btn{
    margin-right: .5em;
    width: 2em;
    height: 2em;
    @include center;
    flex-shrink: 0;
    background-image: url(../img/plus.svg), url(../img/minus.svg);
    @include bgImgSize(100%);
    background-size: 100% auto, 0 auto;
    transition: 0s;
  }
  &__hidden{
    padding: 0 3em 1.5em;
    display: none;
  }
}


@media screen and (max-width: 992px){
  .mortgage{
    overflow: hidden;
    .text{
      column-count: 1;
    }
  }

  .mortgage-slider{
    .swiper-slide{
      width: 19em;
    }
  }

  .mortgage-card{
    padding: 1em;
    &__bg{
      &:after{
        background: linear-gradient(279deg, rgba(252, 251, 249, 0.00) 22.05%, rgba(252, 251, 249, 0.80) 40.65%);
      }
    }
    &__top,
    &__bottom{
      position: relative;
      z-index: 2;
      max-width: 29em;
    }
    &__bottom{
      margin-top: auto;
      padding-top: 2em;
    }
  }

  .qa-card{
    &__show{
      padding: 1em;
    }
    &__hidden{
      padding: 0 2em 1em;
    }
  }
}


