.advantages{
  overflow: hidden;
}

.advantages-slider{
  .swiper-slide{
    width: calc(100% / 3);
  }
  .swiper-button-disabled{
    opacity: 0;
  }
}

.advantage-card{
  &__img{
    @include imgCover(60.5%);
    border-radius: 1em;
  }
  &__content{
    padding: 1.5em;
    @include grid100gap(1em);
  }
}

.faq2{
  background-color: $bgLight;
  &__img{
    width: 100%;
    border-radius: 1.5em;
    display: block;
  }
  .qa-card{
    background-color: $white;
  }
}

.tour{
  background: $bg;
  padding: 3em 0;
  &__col{
    grid-column: span 2;
  }
  &__img{
    width: 100%;
    display: block;
    border-radius: 6em 0;
  }
  &__item{
    padding: 1.5em 1em;
    .h5{
      font-weight: 600;
      text-transform: unset;
    }
  }
}

.build-process{
  &__img{
    border-radius: 1em;
    @include imgCover(82%);
  }
  &__btn{
    ._view2{
      display: none;
    }
    img{
      transition: all .3s;
    }
  }
  .tab-content{
    &._show-all{
      .build-process__btn{
        img{
          transform: rotate(-90deg);
        }
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
    }
    &:not(._show-all){
      .build-process__img:nth-child(8)~.build-process__img{
        display: none;
      }
    }
  }
}

.flat-card{
  border-radius: 1em 1em .25em .25em;
  border: 1px solid #E5E5E7;
  transition: all .3s;
  &:hover{
    box-shadow: $shadowL;
  }
  &__img{
    border-radius: 1em;
    @include imgCover(110.7%)
  }
  &__top{
    padding: 1em 1.5em;
  }
  &__middle{
    padding: 1em 1.5em;
    border-top: 1px solid #E5E5E7;
  }
  &__bottom{
    padding: 1em 1.5em;
    border-top: 1px solid #E5E5E7;
  }
  &__props{
    @include grid100gap(.5em);
  }
  &__prop{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    &:before{
      content: '';
      min-width: 1em;
      flex-grow: 1;
      border-bottom: 1px dashed #E5E5E7;
      margin-top: 1em;
    }
    &-title{
      order: -1;
    }
  }
  &__tags{
    margin-top: -1em;
    display: flex;
    flex-wrap: wrap;
  }
  &__tag{
    margin: 1em 1em 0 0;
    display: flex;
    align-items: center;
    padding: 0 1em;
    background-color: #F8F9FB;
    border-radius: 1em;
    height: 2.5em;
    img{
      width: 1.5em;
      margin-right: .5em;
    }
  }
}


@media screen and (max-width: 992px){
  .advantages-slider{
    .swiper-slide{
      width: 19em;
    }
  }

  .advantage-card{
    &__img{
      padding: 0;
      height: 15.75em;
    }
    &__content{
      padding: 1em;
      gap: .5em;
    }
  }

  .faq2{
    &__col2{
      display: none;
    }
  }

  .tour{
    padding: 1.5em 0;
    &__col{
      grid-column: span 1;
    }
    &__items{
      gap: .5em;
    }
    &__item{
      padding: 1em .5em;
    }
  }

  .build-process{
    &__grid{
      padding: 0 .67em;
      grid-template-columns: repeat(4,calc(25% - 1.5em / 4));
      gap: .5em;
    }
  }

  .flat-card{
    &__top,
    &__middle,
    &__bottom{
      padding: .5em 1em;
    }
    &__props{
      gap: .25em;
    }
    &__prop{
      &:before{
        margin-top: .875em;
      }
    }
    &__tags{
      margin-top: -.5em;
    }
    &__tag{
      margin: .5em .5em 0 0;
      padding: 0 .5em;
      height: 2em;
      img{
        margin-right: .25em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .build-process{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .25em));
    }
  }
}
