.news-collection{
  overflow: hidden;
}

.swiper-button{
  border-radius: 0.25em;
  width: 2.5em;
  height: 2.5em;
  @include center;
  img{
    width: 1.5em;
  }
}

.swiper-pagination{
  position: static;
  margin-top: 1.5em;
  @include center;
  &-bullet{
    width: 2.5em;
    height: 0.25em;
    border-radius: 1px;
    border: 1px solid #E5E5E7;
    opacity: 1;
    background-color: #F8F9FB;
    margin: 0 .25em !important;
    position: relative;
    &:before{
      content: '';
      @include centerAbsolute;
      height: 3em;
      width: 100%;
    }
    &-active{
      background-color: $primary;
      border-color: $primary;
    }
  }
}

.slider{
  position: relative;
  &__wrap{
    overflow: hidden;
  }
  .swiper-button{
    top: 50%;
    margin: 0;
    transform: translateY(-50%);
  }
  .swiper-button-prev{
    left: -3em;
  }
  .swiper-button-next{
    right: -3em;
  }
  .swiper-container{
    width: calc(100% + 1.5em);
  }
  .swiper-wrapper{
    align-items: stretch;
  }
  .swiper-slide{
    height: auto;
    padding-right: 1.5em;
  }
  @media screen and (min-width: 993px){
    .swiper-slide{
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      &-next+.swiper-slide+.swiper-slide,
      &-next+.swiper-slide,
      &-next,
      &-active{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
}

.news-slider{
  .swiper-slide{
    width: 25%;
  }
}

.news-card{
  border-radius: 1em;
  position: relative;
  transition: all .3s;
  &:hover{
    box-shadow: $shadowL;
  }
  &__img{
    border-radius: 1em 1em 0 0;
    height: 14.3em;
    @include imgCover(0);
  }
  &__badges{
    position: absolute;
    display: flex;
    flex-wrap: wrap;
    padding: .5em 1em;
    top: 0;
    left: 0;
  }
  &__badge{
    display: flex;
    align-items: center;
    padding: 0 .75em;
    height: 1.75em;
    background-color: #F8F9FB;
    border-radius: 1em;
    font-weight: 500;
    margin: .5em .5em 0 0;
  }
  &__content{
    padding: 1.5em 1em;
  }
}


@media screen and (max-width: 992px){
  .swiper-pagination{
    margin-top: 1.375em;
  }

  .slider{
    .swiper-button{
      display: none;
    }
    .swiper-container{
      width: calc(100% + 1em);
    }
    .swiper-slide{
      padding-right: 1em;
    }
  }

  .news-slider{
    .swiper-slide{
      width: 19em;
    }
  }

  .news-card{
    border: 1px solid #E5E5E7;
    &__img{
      height: 11.25em;
    }
    &__badges{
      padding: .75em 1em;
    }
    &__badge{
      padding: 0 .5em;
      height: 1.4375em;
      margin: .25em .25em 0 0;
    }
    &__content{
      padding: 1em .5em;
    }
  }
}


