.calc-result{
  padding: 1.5em;
  background-color: $bgLight;
  border-radius: 1.5em;
  &__top{
    padding: 1.5em 0;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
    gap: 1em;
    margin-top: 2.5em;
  }
  &__item{
    text-align: center;
    padding: 1.5em 1em;
    border-radius: 1em;
    border: 1px solid #E5E5E7;
    background: $white;
    .h4{
      font-family: $font3;
    }
    .text-l{
      font-family: $font2;
    }
  }
}

.m-calc{
  iframe{
    min-height: 12em;
  }
  //height: 1230px;
  //iframe{
  //  height: 100% !important;
  //}
}


@media screen and (max-width: 992px){
  .calc-result{
    padding: 1em;
    border-radius: 1em;
    background-color: transparent;
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 1em / 3));
      gap: .5em;
      margin-top: 0;
    }
    &__item{
      padding: 1em .5em;
      background-color: $bgLight;
    }
  }

  .m-calc{
    min-height: 1200px;
  }
}


@media screen and (max-width: 576px){
  .calc-result{
    &__grid{
      grid-template-columns: 100%;
    }
    &__btn{
      width: 100%;
    }
  }
}
