.about-main{
  padding-top: 2.5em;
  background-color: $bgLight;
  &__img{
    img{
      border-radius: 6em 0;
      width: 100%;
      display: block;
    }
  }
  &__col2{
    grid-column: span 2;
  }
}

.team{
  background-color: $bgLight;
}

.team-card{
  position: relative;
  font-weight: 500;
  color: $white;
  display: block;
  border-radius: 1em;
  box-shadow: $shadowXl;
  &__img{
    @include imgCover(100%);
    border-radius: 1em;
  }
  &__info{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 1.5em;
  }
}

.vacancies{
  &__col{
    grid-column: span 2;
  }
  &__quote{
    padding: 1.5em 1.5em 1.5em 2.5em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: .25em;
      height: 100%;
      background-color: $primary;
    }
  }
  &__items{
    gap: 1.5em;
  }
  &__item{
    display: flex;
    align-items: center;
    img{
      width: 1.5em;
      margin: 0 1em 0 .5em;
    }
  }
  &__hh{
    img{
      flex-shrink: 0;
      width: 12.25em;
      margin-left: 1.5em;
    }
    &-text{
      max-width: 17.25em;
    }
  }
}


@media screen and (max-width: 992px){
  .about-main {
    padding-top: 1.5em;
    &__col2{
      grid-column: span 1;
    }
  }

  .team-card{
    &__info{
      padding: 1em;
    }
  }

  .vacancies{
    &__col{
      grid-column: span 1;
    }
    &__quote{
      padding: 1em 1em 1em 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .about-main{
    &__img{
      img{
        border-radius: 3em 0;
        height: 18em;
        object-fit: cover;
      }
    }
  }

  .vacancies{
    &__items{
      flex-direction: column;
      align-items: flex-start;
    }
  }
}
